$spacing: 1em;
$pxPad: 20px;
$contentHeight: 77vh;
$pink: #C08497;
$darkPink: #550527;
$lightOrange: #FFCAD4;
$lightPink: #F7AF9D;
$light: #F4F1BB;

@mixin setSpacing($marginProp, $paddingProp) {
  margin: $marginProp;
  padding: $paddingProp;
}

@mixin setCompatibleProp($prop, $val) {
  #{$prop}: $val;
  -webkit-#{$prop}: $val;
  -moz-#{$prop}: $val;
  -ms-#{$prop}: $val;
  -o-#{$prop}: $val;
}

HTML,
body {
  width: 100%;
}

HTML {
  // background-image: url('./assets/');
  height: 100%;
  position: relative;
  background-size: cover;
}

body {
  min-height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  background: rgba(255, 255, 255, 0.6);
}

#root {
  font-family: Arial;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.flex-row {
  display: flex;
  flex-direction: row;

  @media only screen and (max-width: 900px) {
    display: block;
  }

  margin: $spacing * 2;
}

.content,
.big-history,
.mini-history,
.results,
.res-and-history {
  margin: $spacing;
  @media only screen and (max-width: 900px) {
    margin: 1em auto;
  }
}

#submit-btn {
  background-color: $lightPink;
  border: 2px $lightPink solid;
  color: #ffff;
  &:hover {
    background-color: $pink;
    color: #ffff;
  }
}

.content {
  min-height: $contentHeight;
  margin-bottom: 2.5rem;
}

.zero-opacity {
  opacity: 0;
}

.method{
  margin-top: 20px;
}

textarea{
  width: 50%;
  @include setSpacing($spacing * 2, $spacing * 2);
}