@import '../../base';

.form {
  margin: $spacing;
}

#post-btn{
  background-color: $lightPink;
  border: 2px $lightPink solid;
}
#get-btn{
  background-color: $lightOrange;
  border: 2px$lightOrange solid;
}
#put-btn{
  background-color: $light;
  border: 2px$light solid;
}
#patch-btn{
  background-color: $lightPink;
  border: 2px $lightPink solid;
}
#delete-btn{
  background-color: $pink;
  border: 2px $pink solid;
}
.form-input {
  input {
    @include setSpacing($spacing / 2, $spacing / 2);
  }

  button {
    @include setSpacing($spacing, $spacing $spacing * 2);
  }

  .form-child {
    display: flex;
    flex-direction: row;
    align-items: center;

    label,
    textarea {
      @include setSpacing(0, $spacing);
    }

    textarea {
      resize: none;
    }

    @media only screen and (max-width: 900px) {
      button {
        width: 96%;
      }

      textarea {
        @include setSpacing($spacing / 2 auto, $spacing);
      }

      label {
        display: block;
        text-align: center;
      }
    }
  }

  .url-textbox {
    width: 69.5%;
    overflow: hidden;
  }

  .reqBody-textbox {
    width: 70.75%;
    height: $spacing * 10;
    box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 4px;
    background-color: #f8f8f8;
  }

  .method {
    text-align: center;
    button {
      background-color: #004680;
      color: white;

      &:hover {
        background-color: #3f6181;
        cursor: pointer;
      }
    }

    .selected-req {
      border: 0.4em solid green;
      background-color: $darkPink;
    }
  }

  @media only screen and (max-width: 900px) {
    label {
      padding: $spacing / 2;
    }

    .form-child {
      display: block;
    }

    input.url-textbox {
      width: 96%;
    }

    #submit-btn {
      width: 95%;
    }

    .reqBody-textbox {
      width: 100%;
    }
  }
}